<h1 id="java-中-io-流分为几种" tabindex="-1"><a class="header-anchor" href="#java-中-io-流分为几种" aria-hidden="true">#</a> java 中 IO 流分为几种?</h1>
<ul>
<li>
<p>按照流的流向分，可以分为输入流和输出流；</p>
</li>
<li>
<p>按照操作单元划分，可以分为字节流和字符流；</p>
</li>
<li>
<p>按照流的角色划分，可以分为节点流和处理流；</p>
</li>
</ul>
<p>Java IO流共涉及40多个类，这些类看上去很杂乱，但实际上很有规则，
而且彼此之间存在非常紧密的联系， Java IO流的40多个类大部分都是从如下4个抽象类基类中派生出来的。</p>
<ul>
<li>
<p>InputStream/Reader: 所有的输入流的基类，前者是字节输入流，后者是字符输入流。</p>
</li>
<li>
<p>OutputStream/Writer: 所有输出流的基类，前者是字节输出流，后者是字符输出流。</p>
</li>
</ul>
